@import url(https://fonts.googleapis.com/css?family=Nova+Round&display=swap);
@import url(https://fonts.googleapis.com/css?family=Nova+Round&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(-45deg, #FA8BFF, #2BD2FF, #FFE53B,#FF2525, #FBDA61);
    background-size: 800% 800%;
    animation: gradient 10s ease infinite;
}
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
.header{text-align:center;color:black;margin-bottom:40px;opacity:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}.tooltip{position:fixed;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;background-color:white;padding:5px 12px;border-radius:5px;font-family:Nova Round;display:none;font-size:20px;font:icon;box-shadow:0px 0px 5px 0px rgba(0,0,0,0.15)}.calculator{min-width:273px;min-height:448px;background:#EBECF1;box-shadow:0px 10px 50px rgba(0,0,0,0.15);border-radius:16px;font-family:Nova Round;font-style:normal;font-weight:normal;font-size:24px;line-height:29px;padding:10px 10px;transition:background-color .5s ease-in-out, box-shadow .5s ease-in-out}.calculator .screen{width:233px;height:91px;background-color:#EBECF1;box-shadow:inset 1.5px 1.5px 3px #CBCBCB, inset -1.5px -1.5px 3px #FFFFFF;border-radius:16px;margin:20px 20px 20px 20px;transition:background-color .5s ease-in-out, box-shadow .5s ease-in-out}.calculator .screen .screenContent{text-align:end;padding:17px;font-size:30px}.calculator .screen .screenContent #result{font-size:30px;line-height:36px;margin-bottom:5px;overflow:auto;-webkit-user-select:all;-ms-user-select:all;user-select:all}.calculator .screen .screenContent #result #resultText{color:#373737;transition:color .5s ease-in-out}.calculator .screen .screenContent .calculation{font-size:12px;line-height:14px;color:#999;overflow:auto;transition:color .5s ease-in-out}.calculator .numPad{width:233px;height:296px;margin:20px 20px 20px 20px;display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(5, 1fr);grid-column-gap:19px;grid-row-gap:19px}.calculator .numPad .clear svg{pointer-events:none}.calculator .numPad .style{-webkit-user-select:none;-ms-user-select:none;user-select:none;box-shadow:4.5px 4.5px 9px #CBCBCB, -4.5px -4.5px 9px #FFFFFF;border-radius:500px;display:flex;justify-content:center;align-items:center;cursor:pointer;transition:background-color .5s ease-in-out, box-shadow .5s ease-in-out, color .5s ease-in-out}.calculator .numPad .styleOnClick{box-shadow:inset 2px 2px 4px #CBCBCB, inset -2px -2px 4px #FFFFFF;font-size:20px;opacity:0.8}.calculator .numPad .circle{width:44px;height:44px}.calculator .numPad .numColor{color:#373737}.calculator .numPad .ariOpeColor{color:#2D98DA}.calculator .numPad .zero{width:107px;height:44px;color:#F7B731;grid-area:5 / 1 / 6 / 3}.calculator .numPad .equal{width:44px;height:170px;color:#20BF6B;grid-area:3 / 4 / 6 / 5}

